@import url(https://fonts.googleapis.com/css?family=Goblin+One|Roboto&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#F2F2F2}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{background:black;color:white;text-align:center}.Graphic-Design-1-hex{color:#F2F2F2}.Graphic-Design-2-hex{color:#BFBFBF}.Graphic-Design-3-hex{color:#8C8C8C}.Graphic-Design-4-hex{color:#595959}.Graphic-Design-5-hex{color:#262626}

.App-header{background:#f2f2f2;color:#262626;border-bottom:3px solid;text-align:left;display:grid;grid-template-columns:15% 50% 35%}.App-header nav{display:inline-block}.App-header nav ul{list-style-type:none;margin:0;margin-top:15%;padding:0;overflow:hidden;font-family:Roboto;font-weight:bold;background-color:#f2f2f2}.App-header nav ul li{float:left;margin-right:10px}.App-header nav ul li p{display:block;color:#262626;text-align:center;padding-left:14px;padding-right:14px;text-decoration:none}.App-header nav ul li a{display:block;color:#262626;text-align:center;padding:14px 16px;text-decoration:none}.App-header nav ul li a:hover:not(.active){background-color:#bfbfbf;border-bottom:solid}.App-header nav ul .active{background-color:#4caf50}.App-header .Logo-section{margin-top:10px;width:100%;text-align:center;display:inline-block}.App-header .Logo-section .App-logo{width:50px}.App-header .Logo-section .App-logo p{margin:0;padding:0}.App-header .Logo-section .Title{font-family:"Goblin One", cursive}.App-header .Logo-section .Title-details{font-size:10px;font-family:"Goblin One", cursive}.Contact .Contact-section{display:inline-block;margin:0;padding:0}.Contact .Contact-section h4{display:inline-block;margin:0;padding:0}.Contact .Contact-section a{display:inline-block;margin-left:0.5em;color:gray;text-decoration:none}.Contact .Phone{margin-top:10%}@media only screen and (min-device-width: 375px) and (max-device-width: 812px){.App-header{background:#f2f2f2;color:#262626;border-bottom:3px solid;text-align:left;display:grid;grid-template-columns:25% 75%}.App-header img{display:none}.App-header .Contact{display:none}}

.Homepage .Section1{height:450px;width:100%}.Homepage .Section1 .Blur{-webkit-filter:blur(3px) brightness(0.85);filter:blur(3px) brightness(0.85);background-image:url(/static/media/mba.2f034ffb.jpg);width:100%;height:100%;background-repeat:no-repeat;background-size:cover;position:relative}.Homepage .Section1 .Left-content{font-family:Roboto;position:relative;width:50%;float:left;top:-100%;text-align:left}.Homepage .Section1 .Right-content{text-align:left;top:-100%;margin-top:2%;font-family:Roboto;position:relative;width:35%;margin-right:4%;float:right;color:white;background:#4a4a4a57;border:solid 1px #4f4f4fb3;border-radius:3px;padding:5%}.Homepage .Section2{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;background:#F2F2F2}.Homepage .Section3{height:450px;background:#BFBFBF}

.Brand-promise{margin-left:10%;margin-top:10%}.Brand-promise .Svg{display:block;text-indent:-9999px;width:80px;height:90px}.Brand-promise .Svg.Water-truck{background:url(/static/media/water-truck.817444d8.svg);background-size:80% 100%;background-repeat:no-repeat}.Brand-promise .Svg.Logistics{background:url(/static/media/verification-of-delivery-list-clipboard-symbol.814f555c.svg);background-size:80% 100%;background-repeat:no-repeat}.Brand-promise .Svg.Timely{background:url(/static/media/chronometer.307f5beb.svg);background-size:80% 100%;background-repeat:no-repeat}.Brand-promise .Svg.Transportation{background:url(/static/media/world-grid-with-arrow.36cb4324.svg);background-size:80% 100%;background-repeat:no-repeat}@media only screen and (min-device-width: 375px) and (max-device-width: 812px){.Left-content{display:none}.Left-content .Brand-promise{display:none}.Right-content{margin:2% !important;width:95% !important;float:unset !important;padding:0px !important;font-size:20px !important}}

.Services{color:black}.Services .Title{background:#F2F2F2;width:100%;text-align:center;padding-top:5%;font-size:22px;position:absolute;font-weight:bold}.Services .Title .Title-span{margin-bottom:5%}.Services .Title .Services-grid{font-weight:100;margin-top:5%;width:100%;display:grid;grid-template-rows:200px 200px;grid-template-columns:50% 50%}

